.project
{
    margin:50px 0px;
    padding-left:130px;
}
.project hr{
    color:#138781;
    width:400px;
    margin:10px auto;
}
.project h2{
    font-weight:bold;
    color:#138781;
   
}
#ads
{
    margin:30px 0px 30px 0px;
}
#ads .card-notify-badge
{
    position:absolute;
    left:-10px;
    top:-20px;
    background:#f29f67;
    text-align:center;
    border-radius:30px 30px 30px 30px;
    color:black;
    padding:5px 10px;
    font-size:14px;

}
.card img{
    height:150px;
    width:100%;
}
.card-details-badge
{
    background-color:#e0b50f;
    text-align:center;
    border-radius:30px 30px 30px 30px;
    color:black;
    padding:10px;
    font-size:14px;
}
.card-image-overly
{
font-size:20px;
}
.card-image-overly span
{
display:inline-block;
margin-left:5px;
color:white;
}
.card:hover
{
  background-color: white;
  border-radius:4px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition:all 1s ease
}
.ad-btn
{
    text-transform:uppercase;
    width:150px;
    height:40px;
    display:block;
    text-align:center;
    color:white;
    background-color:#138781;
    color:black;
    border-radius:80px;
    font-size:16px;
    line-height:35px;
    text-decoration:none;
    margin:20px auto 20px auto;
    overflow:hidden;
    position:relative;

}
.ad-btn:hover{
    background-color:white;
    color:#138781;
    background:transparent;
    border:2px solid #138781;
    trasition:all 0.5s ease;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
@media(max-width:600px)
{
    .project
    {
        padding:0;
        margin:0;
        margin-top:20px;
    }
}