.techstack
{
    padding-left:130px;
    margin-top:60px;

}
.techstack hr{
    color:#138781;
    width:400px;
    margin:10px auto;
}
.techstack h2{
    font-weight:bold;
    color:#138781;
   
}
.techstack .card
{
    cursor:pointer;
    padding:30px;
}
.techstack .tech-icon
{
    font-size:2 rem;
    margin-right: 10px;
}
.techstack .card:hover{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    background-color:#138781;
    color:white;
    border:none;
    transition:1s;
}
.card-body .d-flex
{
padding:30px;
}
@media(max-width:600px)
{
    .techstack{
        padding:0px;
        margin:0px;
        margin-top:50px;


    }
    .techstack .hr{
     width:280px;
    }
}