.about
{
    margin:30px 0px;
    padding:20px;
    margin-left: 180px;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
.about-img img{
    border-radius:30px;
    width:75%;
    height:450px;
}
.about-content
{
display:flex;
flex-direction:column;
justify-content:center;
align-items:center;
}
.about-content h1
{
color:grey;
font-family:cursive;
border-bottom:2px solid gray;
text-align:center;
}
.about-content p{
    text-align:justify;
    padding:20px;
}


@media(max-width:600px)
{
    .about{
        margin:0px;
        padding:0;
        margin-top:20px;
        width:100%;
    }
    .about-content  h1{
     margin-top:10px;
    }
    .about-img img {
      margin-left:20px;
    }
}