.home-container
{
    height:400px;
    color:white;
    padding-left:150px;
}
.home-content h2{
    padding:5px;
    padding-top:150px;
}

.home-content h1{
    padding:5px;
   color:#e0b50f;
}


.home-buttons .btn-hire
{
    background-color:#3b8ff3;
    color:white;
    width:160px;
    margin-right:20px;

}
.home-buttons .btn-hire:hover
{
    background-color:white;
    color:#3b8ff3;
    width:200px;
    transition:1s;
}
.home-buttons .btn-cv 
{
    background-color: #f29f67;
    color:white;
    width:160px;
    margin-right:20px;
}
.home-buttons .btn-cv:hover
{
  opacity:80%;
  transition:1s;
}
@media(max-width:600px)
{
    .home-container{
        padding:0;
        margin:0;
        margin-top: 50px;
    

    }
    .home-container h2{

        font-size:24px;
    }
    .home-container h1{
        font-size:24px;
    }

    }
/* ************* */
@media(max-width:600px)
{
    .home-container{
        width:100%;
        padding:0;
        margin:0;
        align:center;
        margin-top:50px;
    }
    .home-container h2{
      font-size:24px;

    }
    .home-container h1 
    {
        font-size:24px;
    }
}